@import 'colors';
@import 'buttons';

:local(.detail) {

    box-sizing: border-box;
    text-align: center;
    color: $white;
    background-color: $item;
    border-radius: 15px;
    // width: 25%;
    // margin: 5% auto;
    padding: 5%;
    margin: 2%;
    // height: 100%;
    // width: 220px;
    // height: 220px;

    button {
        padding: 1%;
        text-align: center;
        background-color: $button;
        width: 75px;
        outline: none;
        border: none;
        border-radius: 15px;
        transition: transform .5s;
    }

    button:hover {
        transform: scale(1.1);
    }

} 



